const carouselSlice = createSlice({
    name: "carousel",
    initialState: {
        cinemaRek: [],
        moveCinema: [],
        rekCinema: [],
        cinemaHorror: [],
        realCinema: [],
        comedyCinema: [],
        sportCinema: [],
        animationCinema: [],
        mostCinema: []
    },
    reducers: {
        addCinemaRek(state, action){
            state.cinemaRek = action.payload.data;
        },
        addImagesTop(state, action){
            state.moveCinema = [...state.moveCinema, ...action.payload.data]
        },
        addRekCinema(state, action) {
            state.rekCinema =  [...state.rekCinema, ...action.payload.data]
        },
        endCinema(state){
            state.cinemaRek = []
        },
        AddCinemaHorror(state, action) {
            state.cinemaHorror =  [...state.cinemaHorror, ...action.payload.data]
        },
        addRealCinema(state, action){
            state.realCinema =  [...state.realCinema, ...action.payload.data]
        },
        addComedyCinema(state, action){
            state.comedyCinema =  [...state.comedyCinema, ...action.payload.data]
        },
        addSportCinema(state, action){
            state.sportCinema =  [...state.sportCinema, ...action.payload.data]
        },
        addAnimation(state, action){
            state.animationCinema =  [...state.animationCinema, ...action.payload.data]
        },
        addMostCinema(state,action){
            state.mostCinema =  [...state.mostCinema, ...action.payload.data]
        }
    }
})

export const {addCinemaRek, addImagesTop, addRekCinema, endCinema, AddCinemaHorror,
    addRealCinema, addComedyCinema, addSportCinema, addAnimation, addMostCinema} = carouselSlice.actions;

export default carouselSlice.reducer;