import React, {useEffect, useState} from 'react';
import { Menu } from 'antd';
import bem from "easy-bem";
import {Link, useHistory, useLocation} from "react-router-dom";

const NavBar = () => {
    const b = bem("navBar")
    const [current, setCurrent] = useState("/");
    const [bool, setBool] = useState(true)
    const {push} = useHistory();
    const link = useLocation();


    useEffect(() => {
        setCurrent(link.pathname)
        if (link.pathname === "/"){
            setBool(true)
        }else {
            setBool(false)
        }
    }, [link.pathname])

    const handleClick = e => {
            setCurrent(e.key);
            if (e.key !== "/"){
                push(e.key);
            }
    };



    return (
        <div className={b()}>
            <div className={b("container_navbar")}>
            <Menu style={{backgroundColor: "transparent", color: "#8c8493", fontSize: "16px", borderBottom: "white"}} onClick={handleClick} selectedKeys={current} mode="horizontal">
                <Menu.Item key="/">
                    {bool ?
                        "Рекомендации"
                        : <Link to="/">
                            Рекомендации
                        </Link>
                    }


                </Menu.Item>
                <Menu.Item key="/sessions">
                    Сеансы
                </Menu.Item>
            </Menu>
            </div>
        </div>
    );
};

export default NavBar;