import React, {useEffect} from 'react';
import {Route, Switch} from "react-router-dom";
import Rek from "../pages/rek/Rek";
import {useDispatch, useSelector} from "react-redux";
import {
    fetchAnimation,
    fetchComedy,
    fetchHorror, fetchMost,
    fetchMove,
    fetchReal,
    fetchRek,
    fetchSport
} from "../redux/carouselSlice";
import InfoCinema from "../component/InfoCinema/InfoCinema";
import Loading from "../component/loading/Loading";
import About from "../pages/about/About";
import Cinema from "../component/cinema/Cinema";
import Sessions from "../pages/sessions/Sessions";

const Router = () => {
    const stateImagesTop = useSelector((state) => state.carousel);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(fetchMove({id: 1}))
        dispatch(fetchRek({id: 1}))
        dispatch(fetchHorror({id: 1}))
        dispatch(fetchReal({id: 1}))
        dispatch(fetchComedy({id: 1}))
        dispatch(fetchSport({id: 1}))
        dispatch(fetchAnimation({id: 1}))
        dispatch(fetchMost({id: 1}))
    },[])


    function endNum(num){
        let str = ""
        for (let i = 0; i < num.length; i++){
            if (+num[i] < 0){
                str += num[i]
            }
        }

        return str
    }

    console.log(endNum("5, 4, -1, 0"))



    if (stateImagesTop.moveCinema.length) {
        return (

                <Switch>
                    <Route exact path='/' component={Rek}/>
                    <Route exact path='/new'>
                        <Cinema dataMove={stateImagesTop.moveCinema} title="Фильмы с высоким рейтингом"/>
                    </Route>
                    <Route exact path='/rek'>
                        <Cinema dataMove={stateImagesTop.rekCinema} title="Рекомендуем вам посмотреть"/>
                    </Route>
                    <Route exact path='/horror'>
                        <Cinema dataMove={stateImagesTop.cinemaHorror} title="Ужасы"/>
                    </Route>
                    <Route exact path='/real_movies'>
                        <Cinema dataMove={stateImagesTop.realCinema} title="Фильмы, основанные на реальных событиях"/>
                    </Route>
                    <Route exact path='/comedies'>
                        <Cinema dataMove={stateImagesTop.comedyCinema} title="Культовые комедии"/>
                    </Route>
                    <Route exact path='/sport'>
                        <Cinema dataMove={stateImagesTop.sportCinema} title="Спортивные фильмы"/>
                    </Route>
                    <Route exact path='/animation'>
                        <Cinema dataMove={stateImagesTop.animationCinema} title="Зарубежные мультфильмы"/>
                    </Route>
                    <Route exact path='/most'>
                        <Cinema dataMove={stateImagesTop.mostCinema} title="Лучшие фильмы"/>
                    </Route>
                    <Route exact path='/about' component={About}/>
                    <Route exact path='/sessions' component={Sessions}/>
                    {stateImagesTop.moveCinema.map((item, i) => <Route key={i} exact
                                                                  path={`/infoCinema:${item.url_id}`}>
                            <InfoCinema id={item.url_id} title={item.title} poster_link={item.poster_link}/>
                        </Route>
                    )}

                    {stateImagesTop.rekCinema.map((item, i) => <Route key={i} exact
                                                                 path={`/infoCinema:${item.url_id}`}>
                            <InfoCinema id={item.url_id} title={item.title} poster_link={item.poster_link}/>
                        </Route>
                    )}

                    {stateImagesTop.cinemaHorror.map((item, i) => <Route key={i} exact
                                                                 path={`/infoCinema:${item.url_id}`}>
                            <InfoCinema id={item.url_id} title={item.title} poster_link={item.poster_link}/>
                        </Route>
                    )}

                    {stateImagesTop.realCinema.map((item, i) => <Route key={i} exact
                                                                    path={`/infoCinema:${item.url_id}`}>
                            <InfoCinema id={item.url_id} title={item.title} poster_link={item.poster_link}/>
                        </Route>
                    )}
                    {stateImagesTop.comedyCinema.map((item, i) => <Route key={i} exact
                                                                       path={`/infoCinema:${item.url_id}`}>
                            <InfoCinema id={item.url_id} title={item.title} poster_link={item.poster_link}/>
                        </Route>
                    )}
                    {stateImagesTop.sportCinema.map((item, i) => <Route key={i} exact
                                                                         path={`/infoCinema:${item.url_id}`}>
                            <InfoCinema id={item.url_id} title={item.title} poster_link={item.poster_link}/>
                        </Route>
                    )}
                    {stateImagesTop.animationCinema.map((item, i) => <Route key={i} exact
                                                                        path={`/infoCinema:${item.url_id}`}>
                            <InfoCinema id={item.url_id} title={item.title} poster_link={item.poster_link}/>
                        </Route>
                    )}

                    {stateImagesTop.mostCinema.map((item, i) => <Route key={i} exact
                                                                            path={`/infoCinema:${item.url_id}`}>
                            <InfoCinema id={item.url_id} title={item.title} poster_link={item.poster_link}/>
                        </Route>
                    )}
                </Switch>
        );
    }

    return    <div className="loading">
         <Loading/>
    </div>
};

export default Router;