import React, {useEffect, useRef, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {Button, Menu} from "antd";
import bem from "easy-bem";
import "./Sessions.css"
import {fetchCarousel} from "../../redux/carouselSlice";
import { Player } from 'video-react';
import {fetchTariff, tariffModal} from "../../redux/infoCinemaSlice";
import Loading from "../../component/loading/Loading";

const Sessions = () => {
    const sessionsState = useSelector(state => state.carousel.cinemaRek);
    const tariffState = useSelector(state => state.infoCinema.tariff);

    const b = bem("navBar")
    const [current, setCurrent] = useState("/today");
    const dispatch = useDispatch();
    let currentTime = new Date();
    let todayDate = currentTime.getDate();
    const handleClick = e => {
        setCurrent(e.key);

    };

    useEffect( () => {
        if (!sessionsState.length){
            dispatch(fetchCarousel());
        }
        dispatch(fetchTariff())
    }, [])



    const tomorrowToday = sessionsState.filter(item => {
        let resDate = new Date(item.session_date);
        let dd = resDate.getDate();
        return dd === todayDate + 1
    })


    const sessionsToday = sessionsState.filter(item => {
        let resDate = new Date(item.session_date);
        let dd = resDate.getDate();

        return dd === todayDate
    })

    const showModal = (id) => {
        dispatch(tariffModal({id}))
    }

    const tomorrowCinema = tomorrowToday.map(item => {
        let resDate = new Date(item.session_date);
        let mm = resDate.getMonth() + 1;
        let dd = resDate.getDate();
        let yy = resDate.getFullYear(); //dd-mm-yy
        let hh = resDate.getUTCHours();
        let min = resDate.getMinutes();
        let n = resDate.getDay();
        let days = [
            'Вс',
            'Пн',
            'Вт',
            'Ср',
            'Чт',
            'Пт',
            'Сб'
        ];

        let arr = [ 'янв', 'фев', 'март', 'апр', 'май', 'июнь', 'июль', 'авг', 'сен', 'ноя', 'дек', ];
        let myDateString = days[n] + ', ' + dd + ' ' + arr[Number(mm) - 1] + ". " + hh + ":" + min;
        return <div className="fons_bg">
            <div key={item.id} className="video_play">
            <Week data={item}/>
            <div className="text_cineam">
                <div className="fons_cinema_sessions" style={{backgroundImage: `url(${item.poster_url})`}}></div>

                <h2>{item.title}</h2>
                <div className="block_sees">
                    <p className="text_age">16+</p>
                    <div className="textSes">
                        <b>Длительность: </b>
                        {item.duration} <br/>
                        <b>Дата: </b> {myDateString}
                    </div>
                </div>
                <div className="mt-3">
                    <h5 className="text-white mt-1 mb-3">Тарифы</h5>
                    <div className="dflex">
                        {tariffState.map(item => {
                            return <div>
                                <Button onClick={() => showModal(item.id)} data-bs-toggle="modal" data-bs-target="#exampleModal" key={item.id} type="dashed" className="btnTariff">{item.title}</Button>
                            </div>
                        })}

                    </div>
                </div>
            </div>
        </div>
        </div>
    })


    const todayCinema = sessionsToday.map(item => {
        let resDate = new Date(item.session_date);
        let mm = resDate.getMonth() + 1;
        let dd = resDate.getDate();
        let yy = resDate.getFullYear(); //dd-mm-yy
        let hh = resDate.getUTCHours();
        let min = resDate.getMinutes();
        let n = resDate.getDay();
        let days = [
            'Вс',
            'Пн',
            'Вт',
            'Ср',
            'Чт',
            'Пт',
            'Сб'
        ];

        let arr = [ 'янв', 'фев', 'март', 'апр', 'мая', 'июнь', 'июль', 'авг', 'сен', 'ноя', 'дек', ];
        let myDateString = days[n] + ', ' + dd + ' ' + arr[Number(mm) - 1] + ". " + hh + ":" + min;
        return <div className="fons_bg">
            <div key={item.id} className="video_play">
            <Week data={item}/>
            <div className="text_cineam">
                <div className="fons_cinema_sessions" style={{backgroundImage: `url(${item.poster_url})`}}></div>
                <h2>{item.title}</h2>
                <div className="block_sees">
                    <p className="text_age">16+</p>
                    <div className="textSes">
                        <b>Длительность: </b>
                        {item.duration} <br/>
                        <b>Дата: </b> {myDateString}
                    </div>
                </div>
                <div className="mt-3">
                    <h5 className="text-white mt-1 mb-3">Тарифы</h5>
                    <div className="dflex">
                        {tariffState.map(item => {
                            return <div>
                                <Button onClick={() => showModal(item.id)} data-bs-toggle="modal" data-bs-target="#exampleModal" key={item.id} type="dashed" className="btnTariff">{item.title}</Button>
                            </div>
                        })}

                    </div>
                </div>
            </div>
        </div>
        </div>
    })

    const sessions = sessionsState.map(item => {
        let resDate = new Date(item.session_date);
        let mm = resDate.getMonth() + 1;
        let dd = resDate.getDate();
        let yy = resDate.getFullYear(); //dd-mm-yy
        let hh = resDate.getUTCHours();
        let min = resDate.getMinutes();
        let n = resDate.getDay();
        let days = [
            'Вс',
            'Пн',
            'Вт',
            'Ср',
            'Чт',
            'Пт',
            'Сб'
        ];

        let arr = [ 'янв', 'фев', 'март', 'апр', 'мая', 'июнь', 'июль', 'авг', 'сен', 'ноя', 'дек', ];
        let myDateString = days[n] + ', ' + dd + ' ' + arr[Number(mm) - 1] + ". " + hh + ":" + min;
        return <div className="fons_bg"> <div key={item.id} className="video_play">
            <Week data={item}/>

            <div className="text_cineam">
                <div className="fons_cinema_sessions" style={{backgroundImage: `url(${item.poster_url})`}}></div>
                <h2>{item.title}</h2>
                <div className="block_sees">
                    <p className="text_age">16+</p>
                    <div className="textSes">
                       <p><b>Длительность: </b>
                        {item.duration} </p>
                        <p>
                        <b>Дата: </b> {myDateString} </p>
                    </div>
                </div>
                <div className="mt-3">
                    <h5 className="text-white mt-1 mb-3">Тарифы</h5>
                    <div className="dflex">
                        {tariffState.map(item => {
                            return <div>
                                <Button onClick={() => showModal(item.id)} data-bs-toggle="modal" data-bs-target="#exampleModal" key={item.id} type="dashed" className="btnTariff">{item.title}</Button>
                            </div>
                        })}

                    </div>
                </div>
            </div>
        </div>
        </div>
    })


    if (sessionsState.length) {
        return (
            <div>
                <div>
                    <div>
                        <div className={`${b()} fons_sessions`}>
                            <div className={b("container_navbar")}>
                                <Menu style={{
                                    backgroundColor: "transparent",
                                    color: "#8c8493",
                                    fontSize: "16px",
                                    borderBottom: "white"
                                }} onClick={handleClick} selectedKeys={current} mode="horizontal">
                                    <Menu.Item key="/today">
                                        Сегодня
                                    </Menu.Item>
                                    <Menu.Item key="/tomorrow">
                                        Завтра
                                    </Menu.Item>
                                    <Menu.Item key="/a_week">
                                        За неделю
                                    </Menu.Item>
                                </Menu>
                            </div>
                        </div>
                    </div>
                    <div className="container_sessions">
                        {current === "/today" ? function () {
                            if (sessionsToday.length) {
                                return todayCinema
                            }

                            return <h2 className="text-white text-center">
                                На сегодня нету сеансов
                            </h2>
                        }() : null}

                        {current === "/tomorrow" ? function () {
                            if (tomorrowToday.length) {
                                return tomorrowCinema
                            }

                            return <h2 className="text-white text-center">
                                На завтра нету сеансов
                            </h2>
                        }() : null}

                        {current === "/a_week" ? function () {
                            if (sessionsState.length) {
                                return sessions
                            }

                            return <h2 className="text-white text-center">Сеансы пока отсутвует</h2>
                        }() : null}

                    </div>
                </div>
            </div>
        )
    }

    return <Loading/>
}

const Week = ({data}) => {
    const refVideo = useRef();

    const startVideo = (id) => {
        refVideo.current.play()
    }

    const stopVideo = (id) => {
        refVideo.current.load()
    }
    return   <div onMouseOver={() => startVideo(data.id)} onMouseLeave={() => stopVideo(data.id)} className="pd">
        <Player
            ref={refVideo}
            muted="muted"
            playsInline
            poster={data.poster_url}
            src={data.trailer_url}
        />
    </div>
}


export default Sessions;
