import React, {useEffect} from 'react';
import InfiniteScroll from "react-infinite-scroll-component";
import {useDispatch} from "react-redux";
import {
    fetchAnimation,
    fetchComedy,
    fetchHorror, fetchMost,
    fetchMove,
    fetchReal,
    fetchRek,
    fetchSport
} from "../../redux/carouselSlice";
import {addIdCinema} from "../../redux/infoCinemaSlice";
import {useHistory} from "react-router-dom";

let count = 1;
let count2 = 1;
let count3 = 1;
let count4 = 1;
let count5 = 1;
let count6 = 1;
let count7 = 1;
let count8 = 1;

const Cinema = ({dataMove, title}) => {
    const dispatch = useDispatch();
    const {push} = useHistory();

    useEffect(() => {
        count = 1
    }, [])
    function removeDuplicates(originalArray, prop) {
        let newArray = [];
        let lookupObject  = {};

        for(let i in originalArray) {
            lookupObject[originalArray[i][prop]] = originalArray[i];
        }

        for(let i in lookupObject) {
            newArray.push(lookupObject[i]);
        }
        return newArray;
    }

    let contentDuplicates = removeDuplicates(dataMove, "url_id");

    const mapCinema = contentDuplicates.map((item, i) => {
       return <div onClick={() => btnInfo(item.url_id)} key={i}>
            <img src={item.poster_link} alt={item.id}/>
        </div>
    })


    const btnInfo = (id) => {
        push(`/infoCinema:${id}`)
        dispatch(addIdCinema({id: id}))
    }

    const fetchMoreTrending = () => {

        if (title === "Фильмы с высоким рейтингом") {
            count++
            setTimeout(() => {
                dispatch(fetchMove({id: count}))
            }, 1000)
        }

        if (title === "Рекомендуем вам посмотреть") {
            count2++
            setTimeout(() => {
                dispatch(fetchRek({id: count2}))
            }, 1500)
        }

        if (title === "Ужасы") {
            count3++
            setTimeout(() => {
                dispatch(fetchHorror({id: count3}))
            }, 1500)
        }
        if (title === "Фильмы, основанные на реальных событиях") {
            count4++
            setTimeout(() => {
                dispatch(fetchReal({id: count4}))
            }, 1500)
        }

        if (title === "Культовые комедии") {
            count5++
            setTimeout(() => {
                dispatch(fetchComedy({id: count5}))
            }, 1500)
        }

        if (title === "Спортивные фильмы") {
            count6++
            setTimeout(() => {
                dispatch(fetchSport({id: count6}))
            }, 1500)
        }

        if (title === "Зарубежные мультфильмы") {
            count7++
            setTimeout(() => {
                dispatch(fetchAnimation({id: count7}))
            }, 1500)
        }

        if (title === "Лучшие фильмы") {
            count8++
            setTimeout(() => {
                dispatch(fetchMost({id: count8}))
            }, 1500)
        }

    }

    return (
        <div className="container_glob">
            <div className="blockCinema">
            <h1 className="textCinema">{title}</h1>
                <InfiniteScroll
                    dataLength={contentDuplicates.length}
                    next={fetchMoreTrending}
                    hasMore={true}
                    loader={ contentDuplicates.length > 24 ? <div className="example">
                        {/*<Spin />*/}
                    </div> : null}
                >
                        <div className="block_card_cinema">
                    {mapCinema}
                        </div>
                </InfiniteScroll>
            </div>
        </div>
    );
};

export default Cinema;
