import React from 'react';
import {useDispatch, useSelector} from "react-redux";
import {Button} from "antd";
import {tariffModal} from "../../redux/infoCinemaSlice";


const About = () => {
    const stateInfoPark = useSelector(state => state.infoCinema.infoPark);
    const tariffState = useSelector(state => state.infoCinema.tariff);
    const dispatch = useDispatch()
    const showModal = (id) => {
        dispatch(tariffModal({id}))
    }

    return (
        <div className="block_gid">
            <iframe
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d731.5625932152898!2d74.60285442922128!3d42.82532478230321!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x389eb7550857e51f%3A0x515c47ec84baa34b!2sSky%20Cinema!5e0!3m2!1sru!2skg!4v1653030279870!5m2!1sru!2skg"
                width="100%"
                height="500"
                frameBorder="0"
                style={{ border: 0 }}
                allowFullScreen=""
                aria-hidden="false"
                tabIndex="0"
            />
            <div className="block_map">
                <div>
                    <div className="text_map">
                    <h4>Бишкек</h4>
                        <p>
                            {stateInfoPark.map(item => item.address)}
                        </p>
                    </div>
                    <div className="fons_map">
                    {stateInfoPark.map(item => {
                        return <div key={item.id} className="text_date">
                            <p><b>По всем вопросам:</b><span> {item.phone_number}</span></p>
                            <p><b>График работы:</b> <span> с 10:00 до 2:00</span></p>
                        </div>
                    })}
                    </div>
                </div>
            </div>
            <h3 className="pt-4 text-white mx-4">Тарифы на сеансы</h3>
            <div className="dflex mx-4">
                {tariffState.map(item => {
                    return <div>
                        <Button onClick={() => showModal(item.id)} data-bs-toggle="modal" data-bs-target="#exampleModal" key={item.id} type="dashed" className="btnTariff">{item.title}</Button>
                    </div>
                })}

            </div>
            <div className=" mx-4 mt-4 mb-5">
                <h3 className="text-white">О компание</h3>
                <div className="text-description">
                {stateInfoPark.map(item => item.description)}
                </div>
            </div>

        </div>
    );
}




export default About;
